/*

github.com style (c) Vasily Polovnyov <vast@whiteants.net>

*/
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  color: #333;
  background: #f8f8f8;
}
.hljs-comment,
.hljs-quote {
  color: #998;
  font-style: italic;
}
.hljs-keyword,
.hljs-selector-tag,
.hljs-subst {
  color: #333;
  font-weight: bold;
}
.hljs-number,
.hljs-literal,
.hljs-variable,
.hljs-template-variable,
.hljs-tag .hljs-attr {
  color: #008080;
}
.hljs-string,
.hljs-doctag {
  color: #d14;
}
.hljs-title,
.hljs-section,
.hljs-selector-id {
  color: #900;
  font-weight: bold;
}
.hljs-subst {
  font-weight: normal;
}
.hljs-type,
.hljs-class .hljs-title {
  color: #458;
  font-weight: bold;
}
.hljs-tag,
.hljs-name,
.hljs-attribute {
  color: #000080;
  font-weight: normal;
}
.hljs-regexp,
.hljs-link {
  color: #009926;
}
.hljs-symbol,
.hljs-bullet {
  color: #990073;
}
.hljs-built_in,
.hljs-builtin-name {
  color: #0086b3;
}
.hljs-meta {
  color: #999;
  font-weight: bold;
}
.hljs-deletion {
  background: #fdd;
}
.hljs-addition {
  background: #dfd;
}
.hljs-emphasis {
  font-style: italic;
}
.hljs-strong {
  font-weight: bold;
}
.post-container {
  max-width: 740px;
  margin: 0 auto;
  padding: 25px;
}
.post-container .comment-link {
    text-decoration: none;
    font-size: 15px;
}
.post-container .post-content {
    margin-top: 15px;
    font-family: Georgia,Cambria,"Times New Roman",Times,serif;
    font-size: 21px;
}
.post-container .post-content h1 {
      margin: 30px 0 15px 0;
}
.post-container .post-content h2 {
      margin: 30px 0 15px 0;
}
.post-container .post-content h3 {
      margin: 30px 0 15px 0;
}
.post-container .post-content h4 {
      margin: 30px 0 15px 0;
}
.post-container .post-content h5 {
      margin: 30px 0 15px 0;
}
.post-container .post-content h6 {
      margin: 30px 0 15px 0;
}
.post-container .post-content ol, .post-container .post-content ul {
      margin-left: 60px;
      margin-bottom: 1rem;
}
.post-container .post-content a > code {
      border: 1px #1976d2 solid;
}
.post-container .post-content code {
      background: #F5F6F6;
      font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
      font-weight: normal !important;
      color: #4B4B4B;
}
.post-container .post-content img {
      max-width: 100%;
}
.post-container .post-content hr {
      height: 1px;
      border: none;
      background: rgba(0, 0, 0, 0.12);
      margin-bottom: 14px;
}
.post-container .post-content table {
      margin-bottom: 2rem;
}
.post-container .post-content pre {
      margin: 10px 0;
}
.post-container .post-content pre .code {
        font-size: 15px;
}
.sharing {
  display: inline-block;
  float: right;
}
.sharing div {
    vertical-align: text-top;
    display: inline-block;
}
.tags {
  display: inline-block;
}



